import Vue from 'vue';
import Vuex from 'vuex'
import App from './App.vue';
import router from './router';
import store from './store';
import axios from 'axios';
import '@/assets/css/base.css';
Vue.prototype.$axios = axios;

Vue.config.productionTip = false;

import 'mescroll.js/mescroll.min.css';

import {
  Lazyload,
  Toast,
  Dialog,
  Panel ,
  Icon,
  Button,
  Cell,
  CellGroup,
  Sidebar,
  SidebarItem,
  Search,
  Form,
  Field,
  Tabs,
  Tab,
  Calendar,
  Popup,
  RadioGroup,
  Radio,
  Stepper,
  Checkbox,
  CheckboxGroup
} from 'vant';
// options 为可选参数，无则不传
Vue.use(Vuex);
Vue.use(Lazyload);
Vue.use(Toast);
Vue.use(Dialog);
Vue.use(Panel);
Vue.use(Cell);
Vue.use(CellGroup);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Sidebar);
Vue.use(SidebarItem);
Vue.use(Search);
Vue.use(Form);
Vue.use(Field);
Vue.use(Tabs);
Vue.use(Tab);
Vue.use(Calendar);
Vue.use(Popup);
Vue.use(RadioGroup);
Vue.use(Radio);
Vue.use(Stepper);
Vue.use(Checkbox);
Vue.use(CheckboxGroup);

//vue-video-player
import 'video.js/dist/video-js.css';
import 'vue-video-player/src/custom-theme.css';
// import 'videojs-contrib-hls'
import VideoPlayer from 'vue-video-player';
Vue.use(VideoPlayer);

import util from '@/utils/util';
Vue.prototype.$utils = util;

import config from '@/utils/config';
Vue.prototype.$config = config;

//微信jsjdk
import wx from 'weixin-js-sdk';
Vue.prototype.$wx = wx;

//引入qs，用于转化post是formdata的问题
import qs from 'qs';
Vue.prototype.$qs = qs;

// require('@/utils/vconsole.js')

store.registerModule('vux', {
  state: {
    direction: 'forward'
  },
  mutations: {}
});
let that = this;

import { getPersonalInfo } from '@/api';
router.beforeEach(function (to, from, next) {
  /* 
    模拟splash启动图功能
    如果 sessionStorage 内没有 enterRecord 这个属性
    则去splash页面 
  */
  if(
    to.name !== 'splash' &&
    !sessionStorage.getItem('enterRecord')
  ){
    next('splash');
  }

  // next();
  if (process.env.NODE_ENV === 'development') {
    next();
  } else {

    if(to.meta.requireAuth){ // 如果下一个路由需要登录凭证

      if (!sessionStorage.getItem('token') && !localStorage.getItem('token')) { // 所有登录凭证都没有，去登录页面
        let hash = location.hash;
        if(hash) sessionStorage.setItem('hash',hash.replace('#',''));
        

        if(to.name === 'myLogin' || to.name === 'register')
          next();
        else
          next('myLogin');

        // return false;
      }else {
        let token = sessionStorage.getItem('token') || localStorage.getItem('token');
        sessionStorage.setItem('token',token);
        getPersonalInfo().then(res=>{
          if (res.code == 0) {
            sessionStorage.setItem("userId", res.data.userCode);
            sessionStorage.setItem("userShop", res.data.userShop);
            sessionStorage.setItem("shopCode", res.data.shopCode);
            sessionStorage.setItem(
              "headimgurl",
              this.$utils.dealImg(res.data.userHeader)
            );
            sessionStorage.setItem("userName", res.data.userName);
            sessionStorage.setItem("userName2", res.data.userName2 || "");
            sessionStorage.setItem("userMobile2", res.data.userMobile2 || "");
          }
        })
        next();
      }
        
    }else
      next();

    
  }
});


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');