//状态，不允许直接对state操作，只能通过mutations或者actions改变
const state = {
    list: []
};

// 暴露属性
const getters = {
    getList(state) { //承载变化的collects
        return state.list;
    }
};

//同步方法
const mutations = {
    setList(state, list) {
        state.list = list;
    }
};

//异步方法
const actions = {
    async initList(context, list) {
        context.commit('setList', list)
    }
};

export default {
    namespaced: true,
    state,
    mutations,
    actions
}